import {render, RenderPosition} from '../framework/render.js';
import TripInfoView from '../view/trip-info-view.js';
import TripInfoMain from '../view/trip-info-main-view.js';
import TripInfoCost from '../view/trip-info-cost-view.js';
import TripFilterView from '../view/trip-filter-view.js';
import { getFilters } from '../mock/filter.js';

export default class TripHeaderPresenter {
  #tripMainContainer = null;
  #tripInfoContainer = null;
  #tripFilterContainer = null;
  #filters = null; // 

  constructor() {
    this.#tripMainContainer = document.querySelector('.trip-main'); //       
    this.#tripInfoContainer = new TripInfoView(); //      ,    
    this.#tripFilterContainer = document.querySelector('.trip-controls__filters'); //   
    this.#filters = getFilters(); //   
  }

  init() {
    render(this.#tripInfoContainer, this.#tripMainContainer, RenderPosition.AFTERBEGIN); //       
    render(new TripInfoMain(), this.#tripInfoContainer.element); //      
    render(new TripInfoCost(), this.#tripInfoContainer.element); //    
    render(new TripFilterView(this.#filters), this.#tripFilterContainer); //   
  }
}
